.features {
  padding: 110px 0 110px;
  border-bottom: 1px solid ;

  &__container {
    display: flex;
  }

  &__tabs {
    border-radius: 4px;
    overflow: hidden;
    flex-shrink: 0;
    margin-right: 100px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1px;
    width: 110px;
    height: 110px;
    background-color: gray;
    color: honeydew;

    &_activ {
      background-color: $orange;
    }
  }



  &__item{
    display: none;
    width: 440px;
    
    &_visible{
      display: block;
    }
  }

}
